"use client";

import axios from "axios";
import { useState } from "react";

export default function QuizForm({ sorular }: any) {
  const [score, SetScore] = useState(0);
  const [isFinish, SetFinish] = useState(false)

  const handleSubmit = (e: any) => {
    e.preventDefault();

    const formData = new FormData(e.target);

    axios
      .post("/api/quiz", formData)
      .then((res) => {
        SetScore(res.data.score);
        SetFinish(true)
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className="bg-transparent max-w-xl mx-auto py-4 bg-indigo-950 bg-opacity-50">
      <form
        onSubmit={handleSubmit}
        className="max-w-xl mx-auto mt-16 py-5 space-y-5"
      >
        {sorular.map((soru: any) => {
          return (
            <div key={soru.id} className="max-w-xl">
              <p className="font-semibold text-xl text-gray-200 opacity-100">
                {soru.soru}
              </p>
              <div className="space-y-2 mt-2 flex flex-col max-w-[140px] mx-auto">
                {soru.secenekler.map((secenek: any) => {
                  return (
                    <label
                      key={secenek.value}
                      className="flex items-center space-x-2"
                    >
                      <input
                        type="radio"
                        name={soru.id}
                        value={secenek.value}
                        className="text-indigo-600"
                      />
                      <span>{secenek.secenek}</span>
                    </label>
                  );
                })}
              </div>
            </div>
          );
        })}
        <button
          type="submit"
          className="px-8 py-3 rounded-lg mt-10 bg-green-500"
        >
          Bitir
        </button>
      </form>
      <div className="text-xl">{isFinish && `Bildiğiniz Soru Sayısı : ${score}`}</div>
    </div>
  );
}
